var gen = require('../../lib/gen-state-machine');

var singleton;

class FractalOfLife {
    /**
     * 
     * @param {*} emerga 
     */
    constructor(emerga) {
        singleton = this;
        this._emerga = emerga;
        this.inited = false;
        this._numIterations = 5;
        this._asyncRender = false;
        this._ui = require('./ui');
        this._drawnElements = 0;

        this.setupStateMachine();
    }
    get ui() {return this._ui;}

    get numIterations() {return this._numIterations;}
    set numIterations(v) {this._numIterations=v;this.settingsUpdated();}
    get asyncRender() {return this._asyncRender;}
    set asyncRender(v) {this._asyncRender=v;this.settingsUpdated();}

    get emerga() {return this._emerga;}
    /**
     * 
     * @param {*} machine 
     * @param {*} el 
     */
    elementEmitted(machine, el) {
        singleton.ui.addToRender(el);
    }
    /**
     * 
     */
    settingsUpdated() {
        singleton.restart();
    }
    /**
     * 
     */
    setupStateMachine() {
        var smRules = null;
        if(this.stateMachine) {
            smRules = this.stateMachine.rules;
        }
        this.stateMachine = new gen.GenerativeStateMachine();
        if(smRules) {
            this.stateMachine.rules = smRules;
        }
        this.stateMachine.asyncCompute = this.asyncRender;
        this.stateMachine.onEmitElement = this.elementEmitted;
        this.stateMachine.onEmitReset = this.settingsUpdated;
    }
    /**
     * 
     */
    restart() {
        if(!singleton.inited) return;
        if(singleton.stateMachine && !singleton.stateMachine.halted) {
            singleton.stateMachine.stop();
            singleton.setupStateMachine();
            singleton.ui.initCanvas(singleton);
            singleton.stateMachine.run(this.numIterations);
        }
    }
    /**
     * 
     */
    init() {
        this.ui.initUI(this);
    }
    /**
     * 
     */
    run() {
        this.inited = true;
        this.stateMachine.run(this.numIterations);
        this.ui.animate(this);
    }
}
module.exports = FractalOfLife;