const color = require('../../lib/color');
const offset = color.offset;

const fullColor = color.fullColor;
const getRgb = color.getRgb;

var g;
var renderSettings;

class RenderingSettings {
    constructor(app) {
        this.app = app;
        this._scaleFactor = 256;
        this._offset = 600;
        this._usePoints =false;
        this._useLines = true;
        this._pointScale = 16;
        this._variablePointSize = false;
        this._variableLineThickness = false;
        this._lineThickness = 1;
        this._lineBrightness = 200;
        this._globalCompositeOperation = 'lighter';
    }
    get scaleFactor() {return this._scaleFactor;}
    get offset() {return this._offset;}
    get usePoints() {return this._usePoints;}
    get useLines() {return this._useLines;}
    get pointScale() {return this._pointScale;}
    get variablePointSize() {return this._variablePointSize;}
    get variableLineThickness() {return this._variableLineThickness;}
    get lineThickness() {return this._lineThickness;}
    get lineBrightness() {return this._lineBrightness;}
    get globalCompositeOperation () {return this._globalCompositeOperation;}
    set scaleFactor(v) {this._scaleFactor=v;this.app.settingsUpdated();}
    set offset(v) {this._offset=v;this.app.settingsUpdated();}
    set usePoints(v) {this._usePoints=v;this.app.settingsUpdated();}
    set useLines(v) {this._useLines=v;this.app.settingsUpdated();}
    set pointScale(v) {this._pointScale=v;this.app.settingsUpdated();}
    set variablePointSize(v) {this._variablePointSize=v;this.app.settingsUpdated();}
    set variableLineThickness(v) {this._variableLineThickness=v;this.app.settingsUpdated();}
    set lineThickness(v) {this._lineThickness=v;this.app.settingsUpdated();}
    set lineBrightness(v) {this._lineBrightness=v;this.app.settingsUpdated();}
    set globalCompositeOperation (v) {this._globalCompositeOperation=v;this.app.settingsUpdated();}
}
/**
 * 
 * @param {*} app 
 */
const initCanvas = (app) => {
    // remove old wrapper
    var curCanv = document.getElementById('wrapper');
    if(curCanv) curCanv.parentNode.removeChild(curCanv);
    // create new wrapper
    var container = document.createElement('div');
    container.id = 'wrapper';
    document.body.appendChild(container);
    // create canvas element
    var canv = document.createElement('canvas');
    canv.id = 'main';
    canv.width = '1800';
    canv.height = '1200';
    container.appendChild(canv);

    app.emerga.initCanvas('main');
    clearCanvas(app);
};
/**
 * 
 * @param {*} app 
 */
const clearCanvas = (app) => {
    app.emerga.context.fillColor = 'rgba(255,255,255,1)';
    app.emerga.context.fillRect(0,0, app.emerga.canvas.width, app.emerga.canvas.height);
};
/**
 * 
 * @param {*} app 
 */
const initToolbar = (app) => {
    var div = document.createElement('div');
    div.id = 'layout';
    div.width = '100%';
    document.body.prepend(div);

    var pstyle = 'background-color: #F5F6F7; border: 1px solid #dfdfdf; padding: 5px;';
    $('#layout').w2layout({
        name: 'layout',
        panels: [
            { type: 'main', style: pstyle, content: 'main', toolbar: {
                name: 'toolbar',
                items: [
                    { type: 'check',  id: 'autoprocess', caption: 'Auto Process', img: 'icon-page', checked: true },
                    { type: 'break',  id: 'break0' },
                    { type: 'menu',   id: 'presets', caption: 'Presets', img: 'icon-folder', items: [
                        { text: 'Item 1', icon: 'icon-page' }, 
                        { text: 'Item 2', icon: 'icon-page' }, 
                        { text: 'Item 3', value: 'Item Three', icon: 'icon-page' }
                    ]},
                    { type: 'break', id: 'break1' },
                    { type: 'spacer' },
                    { type: 'button',  id: 'upload-button',  caption: 'Upload', icon: 'fa-home' },
                    { type: 'button',  id: 'download-button',  caption: 'Download', icon: 'fa-home' },
                    { type: 'spacer' },
                    { type: 'button',  id: 'start-button',  caption: 'Start', icon: 'fa-home' },
                    { type: 'button',  id: 'stop-button',  caption: 'Stop', icon: 'fa-home' },
                    { type: 'button',  id: 'step-button',  caption: 'Step', icon: 'fa-home' },
                    { type: 'button',  id: 'reset-button',  caption: 'Reset', icon: 'fa-home' },
                ]
            }},
            { type: 'right', size: 300, resizable: true, style: pstyle, content: 'right' }
        ]
    });
};
/**
 * 
 * @param {*} app 
 */
const initControls= (app) => {
    g = app.emerga.guis['gui'];

    var folder = g.addFolder('Starting Conditions');
    folder.add(app.stateMachine.rules, 'segments').min(2).step(1).onChange(app.settingsUpdated());
    folder.add(app, 'numIterations').min(1).step(1).onChange(app.settingsUpdated());
    folder.add(app.stateMachine.rules, 'branchAngle', 1, 359).onChange(app.settingsUpdated());
    folder.add(app.stateMachine.rules, 'twistFactor', -10, 10).onChange(app.settingsUpdated());
    folder.add(app.stateMachine.rules, 'segmentStartIndex', 0, 5).onChange(app.settingsUpdated());
    folder.add(app.stateMachine.rules, 'flipNegatives', 0, 5).onChange(app.settingsUpdated());
    folder.add(app.stateMachine.rules, 'skew').onChange(app.settingsUpdated());
    folder.add(app.stateMachine.rules, 'skewAngle',-359,359).onChange(app.settingsUpdated());
    folder.open();

    folder = g.addFolder('Rendering');
    folder.add(app, 'asyncRender').onChange(app.settingsUpdated());
    folder.add(renderSettings, 'usePoints').onChange(app.settingsUpdated());
    folder.add(renderSettings, 'useLines').onChange(app.settingsUpdated());
    folder.add(renderSettings, 'pointScale').onChange(app.settingsUpdated());
    folder.add(renderSettings, 'variablePointSize').onChange(app.settingsUpdated());
    folder.add(renderSettings, 'variableLineThickness').onChange(app.settingsUpdated());
    folder.add(renderSettings, 'lineThickness').onChange(app.settingsUpdated());
    folder.add(renderSettings, 'lineBrightness').onChange(app.settingsUpdated());
    folder.add(renderSettings, 'globalCompositeOperation', [ 
        'source-over', 
        'source-in', 
        'source-out',
        'source-stop',
        'destination-over',
        'destination-in',
        'destination-out',
        'destination-stop',
        'lighter',
        'copy',
        'xor',
        'multiply',
        'screen',
        'overlay',
        'darken',
        'lighten',
        'color-dodge',
        'color-burn',
        'hard-light',
        'difference',
        'exclusion',
        'hue',
        'saturation',
        'color',
        'luminosity'
    ]).onChange(app.settingsUpdated());
    folder.open();
    g.remember(app);
        
    folder = g.addFolder('Scaling');
    folder.add(renderSettings, 'scaleFactor', 1, 8192).onChange(app.settingsUpdated());
    folder.open();
};

const renderElement = (app, el) => {
    var iter = app.stateMachine.iteration;
    var strokeWidth = ( iter > 8 ? 1 : 8 / iter );
    strokeWidth = renderSettings.variableLineThickness ? 
        ( renderSettings.lineThickness * strokeWidth ) / 2 : 
        renderSettings.lineThickness;
    var strokeStyle = fullColor(getRgb(iter), 0.01 * renderSettings.lineBrightness);
    var pointSize = 
        renderSettings.variablePointSize ? 
        renderSettings.scaleFactor / (iter * renderSettings.pointScale) : renderSettings.pointScale;
    var ctx = app.emerga.context;
    ctx.globalCompositeOperation = renderSettings.globalCompositeOperation;
    //
    var drawLine = (start, end) => {
        ctx.strokeStyle = strokeStyle;
        if (renderSettings.useLines) {
            ctx.lineWidth = renderSettings.lineThickness;
            ctx.strokeWidth = strokeWidth;
            ctx.moveTo(start[0], start[1]);
            ctx.lineTo(end[0], end[1]);
        }
        if(renderSettings.usePoints) {
            ctx.lineWidth = 1;
            ctx.fillStyle = strokeStyle;
            ctx.arc(start[0], start[1], pointSize, 0, 2 * Math.PI, false);
            ctx.fill();
                
        } 
    };
    if(app.stateMachine.halted) {
        return clearCanvas(app);
    }
    var originCoords = offset(el.origin(renderSettings.scaleFactor), renderSettings.offset);
    ctx.beginPath();
    el.children.forEach((child) => {
        var childCoords = offset(child.origin(renderSettings.scaleFactor), renderSettings.offset);
        drawLine(originCoords, childCoords);           
        });
    ctx.stroke();
};
/**
 * 
 */
var elementsToRender = [];
const addToRender = (el) => {
    if(Array.isArray(el)) {
        elementsToRender = el;
    }
   else{
    elementsToRender.push(el);
   } 
};
/**
 * 
 * @param {*} app 
 */
const animate = (app) => {
    clearCanvas(app);
    var _animate = () => {
        app.emerga.stats[0].begin();
        var el = elementsToRender.pop();
        while(el) {
            renderElement(app, el);
            el = elementsToRender.pop();
        }
        app.emerga.stats[0].end();
        requestAnimationFrame(_animate);
    };
    requestAnimationFrame(_animate);    
};
/**
 * 
 */
const initUI = (app) => {
    renderSettings = new RenderingSettings(app);
    app.emerga
        .initStats(0)
        .initGUI('gui', {
            load: JSON,
            preset: 'Fractal of Life'
        });
        initToolbar(app);
        initCanvas(app);
        initControls(app);
};

export { initUI, animate, addToRender, initCanvas }